.App {
  font-family: Arial, Helvetica, sans-serif;

  &__clear-button {
    margin-top: 25px;
  }

  &__list {
    list-style-type: none;
    margin: 100px auto 0;
    width: 500px;
  }

  &__list-item {
    margin: 10px;
    display: flex;
    justify-content: space-between;

    &--active {
      background-color: yellow;
    }
  }
}
